body {
  position: relative;
  width: 100vw;
  max-width: 100%;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #fff;
  background-color: #202020;
  cursor: default;

  &::before {
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    opacity: 0.5;
    width: 100%;
    height: 100vh;
    background-image: url("../images/bg.webp");
  }
}

.body-top {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -3;
  height: fit-content;
  max-width: 1300px;

  @include r(1500) {
    max-width: 1200px;
  }

  @include r($xl) {
    max-width: 1000px;
  }
}

a {
  &:active,
  &:hover,
  &:focus {
    outline: 0;
    outline-offset: 0;
    text-decoration: none;
    color: inherit;
  }
}

.container {
  margin: 0 auto;
  padding: 0 50px;
  width: 100%;
  max-width: 1590px;

  &::before,
  &::after {
    display: none;
  }

  @include r(1600) {
    max-width: 1300px;
  }

  @include r($lg) {
    padding: 0 30px;
    max-width: 834px;
  }

  @include r($md) {
    padding: 0 17px;
  }
}

section {
  margin: 0 0 300px;

  @include r(1600) {
    margin: 0 0 250px;
  }

  @include r($xl) {
    margin: 0 0 150px;
  }

  @include r($lg) {
    margin: 0 0 100px;
  }
}

h3 {
  margin-bottom: 100px;
  font-weight: 500;
  font-size: 48px;
  line-height: 72px;

  @include r(1600) {
    margin-bottom: 75px;
    line-height: 1;
  }

  @include r($xl) {
    margin-bottom: 50px;
    font-size: 36px;
    line-height: 42px;
  }

  @include r($lg) {
    margin-bottom: 35px;
    font-size: 24px;
    line-height: 36px;
  }

  @include r($md) {
    margin-bottom: 20px;
    text-align: center;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 63px;
  height: 56px;
  outline: 0;
  outline-offset: 0;
  border: none;
  border-radius: 90px;
  box-shadow: none;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  text-decoration: none;
  background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
  transition: box-shadow 0.2s, color 0.2s;

  &:hover,
  &:active,
  &:disabled,
  &:visited,
  &:focus {
    outline: 0;
    outline-offset: 0;
    text-decoration: none;
    color: inherit;
    background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
  }

  &:hover {
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.25);
  }

  &:active {
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.35), inset 0 4px 15px rgba(255, 255, 255, 0.39);
  }

  &:disabled {
    box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.25);
    color: #dfdfdf;
  }

  @include r(1600) {
    padding: 0 55px;
    height: 50px;
    font-size: 18px;
    line-height: 26px;
  }

  @include r($xl) {
    padding: 0 45px;
    font-size: 16px;
    line-height: 24px;
  }

  @include r($lg) {
    padding: 0 63px;
  }
}

.btn-content {
  margin: 35px 0 0;
  width: fit-content;

  @include r($md) {
    margin: 25px auto 0;
    padding: 0;
    width: 200px;
  }
}

.popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(#000, 0.8);
  transition: all 0.2s;

  &__wrapper {
    position: relative;
    padding: 107px 50px 68px;
    width: 100%;
    max-width: 800px;
    border-radius: 20px;
    background-color: #fff;

    @include r($xl) {
      padding: 90px 30px 55px;
    }

    @include r($lg) {
      padding: 84px 18px 50px;
      max-width: 774px;
    }
  }

  h2 {
    margin: 0 auto 25px;
    width: fit-content;
    max-width: 600px;
    font-weight: 500;
    font-size: 48px;
    line-height: 72px;
    text-align: center;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
    background-clip: text;

    @include r(1600) {
      font-size: 44px;
      line-height: 58px;
    }

    @include r($xl) {
      font-size: 40px;
      line-height: 48px;
    }

    @include r($lg) {
      font-size: 32px;
      line-height: 48px;
    }
  }

  &__close {
    position: absolute;
    right: 66px;
    top: 54px;
    cursor: pointer;

    @include r($lg) {
      right: 52px;
      top: 45px;
    }

    @include r($md) {
      right: 30px;
      top: 40px;
    }

    @include r($sm) {
      right: 16px;
      top: 35px;
    }
  }

  .btn {
    margin: 0 auto;
    width: fit-content;
  }

  &--active {
    visibility: visible;
    opacity: 1;
  }

  @include r($lg) {
    padding: 0 30px;
  }

  @include r($md) {
    padding: 0 17px;
  }
}

.cookie {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 130px;
  z-index: 20;
  margin: 0 auto;
  padding: 0 50px;
  width: 100%;
  max-width: 1590px;
  transition: all 0.2s;

  &__box {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 84px 50px 50px;
    border-radius: 20px;
    background-color: #fff;

    @include r(1600) {
      padding: 60px 40px 40px;
    }

    @include r($xl) {
      padding: 55px 35px 35px;
    }

    @include r($lg) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 69px 8px 35px;
    }
  }

  h4 {
    margin: 0 0 15px;
    width: fit-content;
    font-weight: 600;
    font-size: 24px;
    line-height: 1.2;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
    background-clip: text;

    @include r(1600) {
      margin-bottom: 10px;
      font-size: 22px;
    }

    @include r($xl) {
      font-size: 20px;
    }

    @include r($lg) {
      margin: 0 auto 25px;
      text-align: center;
    }
  }

  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    color: #454545;

    a {
      -webkit-text-fill-color: transparent;
      text-fill-color: transparent;
      background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
      background-clip: text;
    }

    @include r(1600) {
      font-size: 18px;
    }

    @include r($xl) {
      font-size: 16px;
    }

    @include r($lg) {
      margin-bottom: 25px;
      font-size: 14px;
      text-align: center;
    }
  }

  .btn {
    margin-left: 80px;

    @include r($xl) {
      margin-left: 60px;
    }

    @include r($lg) {
      margin: 0 auto;
    }
  }

  &__close {
    position: absolute;
    right: 56px;
    top: 56px;
    cursor: pointer;

    @include r(1600) {
      top: 40px;
    }

    @include r($lg) {
      right: 21px;
      top: 26px;
    }
  }

  &--inactive {
    visibility: hidden;
    opacity: 0;
  }

  @include r(1600) {
    max-width: 1300px;
  }

  @include r($lg) {
    bottom: 215px;
    padding: 0 30px;
    max-width: 834px;
  }

  @include r($md) {
    bottom: 167px;
    padding: 0 17px;
  }
}

.form-accept {
  padding-left: 15px;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #fff;

  @include r($lg) {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
  }

  @include r($md) {
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
  }
}

.geometry {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 0;
  width: 100%;
  height: 1000px;
  max-width: 1000px;
  background-color: transparent;
  //filter: blur(80px);

  &::after,
  &::before {
    position: absolute;
    z-index: -3;
    background-color: transparent;
  }

  &::before {
    content: "";
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 664px;
    max-width: 714px;
    background-image: url("../images/triangle.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include r(1600) {
      height: 459px;
      max-width: 491px;
    }

    @include r($xl) {
      height: 365px;
      max-width: 390px;
    }

    @include r($md) {
      height: 240px;
      max-width: 259px;
    }
  }

  &::after {
    content: "";
    right: 0;
    bottom: 0;
    z-index: 1;
    width: 100%;
    height: 825px;
    max-width: 825px;
    border-radius: 50%;
    background-image: linear-gradient(143.15deg, #6100ff 8.37%, rgba(0, 255, 255, 0) 91.49%);

    @include r(1600) {
      height: 550px;
      max-width: 550px;
    }

    @include r($xl) {
      height: 400px;
      max-width: 400px;
    }

    @include r($md) {
      height: 250px;
      max-width: 250px;
    }
  }

  @include r(1600) {
    height: 800px;
    max-width: 800px;
  }

  @include r($xl) {
    height: 536px;
    max-width: 511px;
  }

  @include r($md) {
    height: 364px;
    max-width: 361px;
  }
}

.geometry-small {
  position: absolute;
  left: -10%;
  top: 50%;
  z-index: -3;
  width: 581px;
  height: 568px;
  background-color: transparent;
  @include r($md) {
transform: scale(0.5);
  }
}

.geometry-bottom {
  &::after,
  &::before {
    display: none;
  }
}

.geometry-top {
  overflow: hidden;
  top: 0;
  z-index: -3;
  bottom: auto;
  //filter: blur(60px);

  &::before {
    animation: triangle 10s linear reverse infinite;
  }

  &::after {
    animation: circle 10s linear infinite;
  }
}

@keyframes circle {
  from {
    transform: rotate(0deg) translateX(40px) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translateX(40px) rotate(-360deg);
  }
}

@keyframes triangle {
  from {
    transform: rotate(0deg) translateX(40px) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translateX(40px) rotate(-360deg);
  }
}